

























import {
	mapState
} from 'vuex';
import {
	childMixin
} from "../../../common/mixins.js";
export default {
	data() {
		return {
			start_clientX: 0,
			moveX: 0,
			device_list: []
		}
	},
	mixins: [childMixin],
	onLoad: function() {
		this.initData();
	},
	onShow: function() {
		let that = this;
		this.BLEDriverData = this.$store.state.BLEDriverData;
	},
	onUnload: function() {
		wx.hideLoading();
	},
	computed: {
		...mapState({ //这里的...不是省略号了,是对象扩展符
			$globalData: 'globalData'
		})
	},
	methods: {
		initData() {
			let that = this;
			let data = {
				userCode: this.$store.state.userCode,
				token: this.$store.state.token
			}
			console.log(this.$store.state.userCode, this.$store.state.token)
			this.$api.userDeviceList(data, function(res) {
				console.log(res);
				if (res.data.status == 1) {
					that.setDataList(res.data);
				}
			}, function(err) {
				console.log(err)
			});
		},
		setDataList(value) {
			let that = this;
			value.data.map(function(item, index) {
				that.device_list.push({
					device_code: item.device_code,
					device_name: item.device_name,
					status: true,
					moveX: 0
				});
			});
		},
		touchstart(e) {
			this.start_clientX = e.clientX;
		},
		touchmove(e, item) {
			let x = e.clientX - this.start_clientX;
			if (x < 0 && x > -40) {
				item.moveX = -76;
			}
			if (x > 0 && x < 40) {
				item.moveX = 0;
			}
		},
		touchend(e) {
			this.start_clientX = 0;
		},
		deleteFn(item) {
			let that = this;
			let data = {
				userCode: this.$store.state.userCode,
				token: this.$store.state.token,
				deviceCode: item.device_code,
			}
			console.log(data);
			this.$api.deleteUserConnect(data, function(res) {
				console.log(res);
				if (res.data.status == 1) {
					uni.showToast({
						title: '删除成功',
						mask: false,
						duration: 1500
					});
					item.status = false;
				} else if (res.data.status == 99) {
					that.loginOut(res.data.msg);
				} else {
					uni.showToast({
						title: '删除失败' + res.data.msg,
						mask: false,
						icon: 'none',
						duration: 1500
					});
				}
			}, function(err) {
				console.log(err)
			});
		}
	},
}



